.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 99; }
  .modal .scroll-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    max-height: 100%;
    width: 100%;
    padding: 30px 16px;
    overflow-y: auto;
    z-index: 2;
    transform: translate(-50%, -50%); }
  .modal .cross {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    border: 0;
    background-color: transparent;
    opacity: 0.5;
    cursor: pointer;
    padding: 0;
    margin: 0;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    transition: all 0.3s ease-out; }
    .modal .cross:before {
      content: '';
      position: absolute;
      width: 20px;
      height: 2px;
      border-radius: 1px;
      background-color: #ffffff;
      transform: rotate(45deg);
      transition: all 0.3s ease-out; }
    .modal .cross:after {
      content: '';
      position: absolute;
      width: 20px;
      height: 2px;
      border-radius: 1px;
      background-color: #ffffff;
      transform: rotate(-45deg);
      transition: all 0.3s ease-out; }
    .modal .cross:hover {
      opacity: 1; }
  .modal .modal-body {
    display: block;
    position: relative;
    max-width: 450px;
    width: 100%;
    border: 1px solid #354c83;
    background: #17264a;
    border-radius: 8px;
    box-shadow: 0 0 29px rgba(29, 20, 62, 0.32);
    padding: 40px 32px 32px;
    margin: 0 auto; }
    @media screen and (max-width: 640px) {
      .modal .modal-body {
        padding: 40px 16px 32px; } }
  .modal .head-modal {
    display: block;
    text-align: center; }
    .modal .head-modal .title {
      display: block;
      font: 400 20px/1.3 "Rogan-Regular";
      color: #ffffff; }
  .modal .modal-content {
    display: block; }

